
import React, { useRef, useState } from 'react';
import cookie from 'react-cookies';
import Login from '../pages/Login';


const axios = require('axios');


function Match({ match, bets }) {

    



    const TeamA = match.competitors[0];
    TeamA.score = match.scores[0].value;
    const TeamB = match.competitors[1];
    TeamB.score = match.scores[1].value;
    const inputScoreA = useRef();
    const inputScoreB = useRef();



    return (
        <div className="Match flex" onMouseLeave={
            () => {
                if (cookie.load('login')) {
                    axios.post('../api/sendBet', {
                        scoreA: inputScoreA.current.value,
                        scoreB: inputScoreB.current.value,
                        idMatch: match.id,
                        user: cookie.load('user')
                    })
                        .then(function (response) {
                            cookie.save('bets', response.data, { path: '/' });
                        })
                        .catch(function (error) {
                            console.log(error);
                        });
                }


            }
        }>
            <div className="TeamA flex">
                <img src={TeamA.logo} alt={TeamA.abbreviatedName} />
                <div className="tag">{TeamA.abbreviatedName}</div>
                <div className="score">
                    <div className="value">
                        {TeamA.score}
                    </div>
                    {cookie.load('login') ?
                        <ModifScore inputScoreA={inputScoreA} inputScoreB={inputScoreB} match={match} team="A" bets={bets} ></ModifScore>
                        : ''}
                </div>
            </div>
            <div className="separator">
            </div>
            <div className="TeamB flex">
                <img src={TeamB.logo} alt={TeamB.abbreviatedName} />
                <div className="score">
                    <div className="value">
                        {TeamB.score}
                    </div>
                    {cookie.load('login') ?
                        <ModifScore inputScoreA={inputScoreA} inputScoreB={inputScoreB} match={match} team="B" bets={bets} ></ModifScore>
                        : ''}
                </div>
                <div className="tag">{TeamB.abbreviatedName}</div>
            </div>
            <div className="myBets">
            </div>
        </div>
    )

}
function ModifScore({ match, inputScoreA, inputScoreB, team, bets }) {

    const [today] = useState(Date.now());
    console.log(bets)

    const [startMatch] = useState((Number.isInteger(match.startDate)) ? match.startDate : match.startDateTS)

    if (today - startMatch <= 0) {
        if (team === "A") {
            return (
                <div className="bet">
                    <input type="text" name="TeamA" className="scoreInput" ref={inputScoreA} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreA :
                            ''
                    } />
                    <div className="lilbtn" onClick={
                        () => {
                            const score = parseInt(inputScoreA.current.value);
                            (isNaN(parseInt(inputScoreB.current.value))) ? inputScoreB.current.value = 0 : inputScoreB.current.value = parseInt(inputScoreB.current.value);
                            (isNaN(score)) ? inputScoreA.current.value = 1 : (score + 1 > 5) ? inputScoreA.current.value = 5 : inputScoreA.current.value = score + 1;
                        }
                    }>+</div>

                    <div className="lilbtn" onClick={
                        () => {
                            const score = parseInt(inputScoreA.current.value);
                            (isNaN(parseInt(inputScoreB.current.value))) ? inputScoreB.current.value = 0 : inputScoreB.current.value = parseInt(inputScoreB.current.value);
                            (isNaN(score) || (score - 1) < 0) ? inputScoreA.current.value = 0 : inputScoreA.current.value = score - 1;
                        }
                    }>-</div>
                </div>
            )
        } else {
            return (
                <div className="bet">
                    <input type="text" name="TeamB" className="scoreInput" ref={inputScoreB} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreB :
                            ''
                    } />
                    <div className="lilbtn" onClick={
                        () => {
                            const score = parseInt(inputScoreB.current.value);
                            (isNaN(parseInt(inputScoreA.current.value))) ? inputScoreA.current.value = 0 : inputScoreA.current.value = parseInt(inputScoreA.current.value);
                            (isNaN(score)) ? inputScoreB.current.value = 1 : (score + 1 > 5) ? inputScoreB.current.value = 5 : inputScoreB.current.value = score + 1;
                        }
                    }>+</div>

                    <div className="lilbtn" onClick={
                        () => {
                            const score = parseInt(inputScoreB.current.value);
                            (isNaN(parseInt(inputScoreA.current.value))) ? inputScoreA.current.value = 0 : inputScoreA.current.value = parseInt(inputScoreA.current.value);
                            (isNaN(score) || (score - 1) < 0) ? inputScoreB.current.value = 0 : inputScoreB.current.value = score - 1;
                        }
                    }>-</div>
                </div>
            )
        }



    }
    else {

        if (team === "A") {
            return (
                <div className="bet">
                    <input type="text" name="TeamA" className="scoreInput" ref={inputScoreA} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreA :
                            ''
                    } />
                </div>)
        } else {
            return (
                <div className="bet">
                    <input type="text" name="TeamB" className="scoreInput" ref={inputScoreB} disabled defaultValue={
                        (bets && bets.hasOwnProperty(match.id)) ?
                            bets[match.id].scoreB :
                            ''
                    } />
                </div>)
        }

    }


}


export default Match;