import React, { useRef, useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Modal from '../../components/Modal';


const axios = require('axios');


function Login({ history }) {



    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false) {
        setModal({
            show: !modal.show,
            message: (clean) ? modal.message : ""
        })
    }
    function sendLogin() {
        setModal({
            show: true,
            message: ""
        });
        axios.post('api/login', {
            login: login.current.value,
            pass: password.current.value
        })
            .then(function (response) {
                if (response.data.status) {
                    console.log(response.data.data)
                    cookie.save('bets', response.data.data.bets, { path: '/' });
                    delete response.data.data.mdp;
                    delete response.data.data.mail;
                    response.data.data.bets = "";
                    cookie.save('login', true, { path: '/' });
                    cookie.save('user', response.data.data, { path: '/' });

                    setModal({
                        show: true,
                        message: response.data.message
                    });
                    setTimeout(() => {
                        setModal({
                            show: false,
                            message: ""
                        });
                        history.push('/')
                    }, 3000)
                } else {
                    setModal({
                        show: true,
                        message: response.data.message
                    });
                }
                setTimeout(() => {
                    setModal({
                        show: false,
                        message: ''
                    });
                }, 2500)
            })
            .catch(function (error) {
                console.log(error);
            });

    }

    const login = useRef();
    const password = useRef();

    return (
        <div>
            <Modal modal={modal} toggleModal={toggleModal}></Modal>
            <h2>Login</h2>
            <div className="loginFrom">
                <label htmlFor="login">Pseudo :</label>
                <input type="text" name="login" id="login" ref={login} />
                <label htmlFor="password">Password :</label>
                <input type="password" name="password" id="password" ref={password} />
                <div className="btn" type="submit" onClick={sendLogin}>Validez</div>
                <Link to="/signIn">
                    <div className="btn" type="submit">S'inscrire</div>
                </Link>
            </div>
        </div>
    )
}



export default withRouter(Login);