import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';



import Match from "../../components/Match";

const axios = require('axios');

function Weeks(props) {
    const maxWeek = props.weeks.length;

    const [currentWeek, setCurrentWeek] = useState(0);

    const [bets, setBets] = useState((cookie.load('bets')) ? cookie.load('bets') : false)
    
    if(cookie.load('login') && bets == false){
        
         axios.get(`../api/getPlayer/${cookie.load('user').login}`)
                 .then(function (response) {
                      setBets(response.data.bets)
                      console.log("VERIF BETS !!!!", response.data.bets)
                 })
                 .catch(function (error) {
                     console.log(error);
                 });
    }

    const weeks = props.weeks.map(week => {
        return <Week week={week} key={week.id} current={currentWeek} bets={bets} />
    })
    return (<div>
        <h2>Matches</h2>
        <div className="changeWeek flex">
            <div className="down" >
                <i onClick={
                    () => {
                        if (currentWeek !== 0) {
                            setCurrentWeek(currentWeek - 1)
                        }
                    }
                }>
                    {"<"}
                </i>
            </div>
            <div className="up">
                <i onClick={
                    () => {
                        if (currentWeek + 1 !== maxWeek) {
                            setCurrentWeek(currentWeek + 1)
                        }

                    }
                }>
                    {">"}
                </i>
            </div>


        </div>
        <div className="Weeks">
            {weeks}
        </div>
    </div>


    );

}
function Week({ week, current, bets }) {
    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var time = date + ' ' + month + ' ' + year;
        return time;
    }
    const Matchs = week.matches.map(match => {
        return <Match match={match} key={match.id} bets={bets} />
    })
    return (
        <div className={(week.id === current) ? "Week show" : "Week"}>
            <div className="info flex ">
                <div className="name">
                    {week.name}
                </div>

                <div className="date start">
                    {timeConverter(week.startDate)}
                </div>
                <div className="date separator">-</div>
                <div className="date end">
                    {timeConverter(week.endDate)}
                </div>

            </div>

            <div className="Matchs flex center">
                {Matchs}
            </div>

        </div>
    )
}


export default withRouter(Weeks);